@import url(https://rsms.me/inter/inter.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}



html {
  font-family: 'Inter', sans-serif;
  overflow: hidden;
}

@supports (font-variation-settings: normal) {
  html {
    font-family: 'Inter var', sans-serif;
  }
}

* {
  box-sizing: border-box;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

div.root {
  width: 100vw;
  height: 100vh;
  padding: 12px;
  background-color: #f1f2f6;
  display: grid;
  grid-column-gap: 12px;
  -webkit-column-gap: 12px;
          column-gap: 12px;
  grid-template-columns: minmax(800px, 1fr) minmax(0, 1fr);
}

g.key-event>circle {
  fill: #999;
  opacity: .5;
  mix-blend-mode: multiply;
}

g.key-event>foreignObject {
  mix-blend-mode: multiply;
}

g.key-event>path {
  stroke: #f1f2f6;
  stroke-width: 1px;
  mix-blend-mode: multiply;
}

.view-container {
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 6.4px 14.4px 0 rgb(0 0 0 / 9%), 0 1.2px 3.6px 0 rgb(0 0 0 / 7%);
  padding: 24px;
}

#view-1 {
  width: 100%;
  height: 680px;
}

/* #view-1>g>rect {
  transition: y 0.3s, height .3s;
} */

div.event-text {
  display: flex;
  justify-content: center;
  mix-blend-mode: multiply;
}

span.event-text {
  color: #636568;
  font-size: 12px;
  background-color: #f1f2f6;
  border-radius: 4px;
  padding: 2px 6px;
  display: inline-block;
}

h1 {
  font-size: 18px;
  font-weight: 700;
  color: #636568;
  -webkit-font-feature-settings: "tnum";
          font-feature-settings: "tnum";
}

span.keyword {
  line-height: 2;
  font-size: 12px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  /* min-width: 96px; */
  border-radius: 4px;
  color: #636568;
  background-color: #fff;
  font-weight: 500;
  text-align: center;
  padding: 2px 8px;
  margin-right: 8px;
  transition: opacity 0.2s, -webkit-filter 0.2s;
  transition: filter 0.2s, opacity 0.2s;
  transition: filter 0.2s, opacity 0.2s, -webkit-filter 0.2s;
  cursor: pointer;
  margin-bottom: 8px;
  -webkit-font-feature-settings: 'tnum',;
          font-feature-settings: 'tnum',
}

span.keyword:hover {
  background-color: #fff!important;
  -webkit-filter: brightness(0.9);
          filter: brightness(0.9);
}

.link-button {
  color: #486df1;
  transition: opacity 0.2s, -webkit-filter 0.2s;
  transition: filter 0.2s, opacity 0.2s;
  transition: filter 0.2s, opacity 0.2s, -webkit-filter 0.2s;
  cursor: pointer;
  font-size: 12px;
}

.link-button:hover {
  text-decoration: underline;
  -webkit-filter: brightness(0.9) saturate(1.1);
          filter: brightness(0.9) saturate(1.1);
}

