#view-2 {
  width: 100%;
  height: 720px;
  stroke-linecap: round;
  -webkit-animation: rotate;
          animation: rotate;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  position: relative;
  top: 200px;
}

@-webkit-keyframes growline {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes growline {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

